import { Component, OnInit } from '@angular/core';
import { OutboundConfigurationService } from 'src/app/service/outbound-configuration.service';
import { DataService } from 'src/app/service/data.service';

@Component({
  selector: 'app-outbound-configuration',
  templateUrl: './outbound-configuration.component.html',
})
export class OutboundConfigurationComponent implements OnInit {
  currentCompany: any;
  company_id
  objectDetails: any;
  is_edit: boolean = false;
  is_add: boolean = false;

  constructor(
    private dataService: DataService,
    private outboundConfigurationService: OutboundConfigurationService
  ) {
    this.dataService.currentCompany.subscribe((res: any) => {
      if (res && res.company_id) {
        this.currentCompany = res;
        this.company_id = res.company_id;
        this.getData();
      }
    });
  }

  ngOnInit(): void {
  }

  getData() {
    this.outboundConfigurationService.getOutboundConfigurationByCompanyId(this.company_id).subscribe((res) => {
      if (res.success) {
        this.objectDetails = res.data;
        this.is_edit = true;
      } else {
        this.is_add = true;
      }

    });
  }
}
