import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable()
export class OutboundConfigurationService {

  API_URL: string = environment.APIEndpoint;

  constructor(
    private http: HttpClient,
  ) { }


  addOutboundConfiguration(data): Observable<any> {
    return this.http.post(`${this.API_URL}/outbound_config`, data);
  }

  editOutboundConfiguration(id, data): Observable<any> {
    return this.http.put(`${this.API_URL}/outbound_config/${id}`, data);
  }

  getOutboundConfigurationByCompanyId(id, param = null): Observable<any> {
    return this.http.get(`${this.API_URL}/outbound_config/${id}`, { params: param });
  }

  getOutboundConfigurationMasterData(): Observable<any> {
    return this.http.get(`${this.API_URL}/outbound_config/master-data`);
  }

}
