import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { OutboundConfigurationEditComponent } from './outbound-configuration-edit/outbound-configuration-edit.component';
import { OutboundConfigurationComponent } from './outbound-configuration.component';
import { Routes, RouterModule } from '@angular/router';
import { SharedModule } from 'src/app/shared/shared.module';
import { FormValidationModule } from 'src/app/shared/form-validation/form-validation.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { OutboundConfigurationService } from 'src/app/service/outbound-configuration.service';
import { NgSelectModule } from '@ng-select/ng-select';

const routes: Routes = [
  {
    path: '',
    component: OutboundConfigurationComponent,
    data: { title: 'view_outbound_configuration' }
  },
  {
    path: 'edit/:id',
    component: OutboundConfigurationEditComponent,
    data: { title: 'edit_outbound_configuration' }
  },
  {
    path: "add",
    component: OutboundConfigurationEditComponent,
    data: { title: 'add_outbound_configuration' }
  }
]


@NgModule({
  declarations: [OutboundConfigurationEditComponent, OutboundConfigurationComponent],
  imports: [
    CommonModule,
    ReactiveFormsModule,
    FormsModule,
    FormValidationModule,
    SharedModule,
    NgSelectModule,
    RouterModule.forChild(routes),
  ], providers: [OutboundConfigurationService]
})
export class OutboundConfigurationModule { }
